<script setup lang="ts">
import { defineModel, defineEmits, ref, watch, inject } from "vue";
import CountrySelect from "@/components/PhoneEnter/CountrySelect.vue";
import PhoneInput from "@/components/PhoneEnter/PhoneInput.vue";
import { ICountry } from "@/services/sign-in/model";
import { signInService } from "@/services/sign-in/service";
import { useSignInStore } from "@/store";
import { useI18n } from "vue-i18n";

interface IEmits {
  (emit: "onNext"): void;
}

const country = defineModel<ICountry>("country");
const phone = defineModel("phone");
const emit = defineEmits<IEmits>();

const store = useSignInStore();

const { t } = useI18n();

const isLoading = ref(false);
const phoneError = ref("");

/* eslint-disable */ // —Ä—É–≥–∞–µ—Ç—Å—è –Ω–∞ —Å–∏–º–≤–æ–ª—ã –≤ regex
const phoneRegexes: any = {
  russia:
    /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/, // —Ç–µ—Å—Ç–∏–ª–∏—Å—å —Ç–æ–ª—å–∫–æ –†–£
  kazakhstan:
      /^(\+7|8)?[\s\-]?\(?7\d{2}\)?[\s\-]?\d{3}[\s\-]?\d{2}[\s\-]?\d{2}$/, // –∏ –ö–ó –Ω–æ–º–µ—Ä
  kyrgyzstan: /^(\+?996|0)?[\s\-]?\(?\d{3}\)?[\s\-]?\d{3}[\s\-]?\d{3}$/,
  uzbekistan: /^(\+?998|0)?[\s\-]?\(?\d{2}\)?[\s\-]?\d{3}[\s\-]?\d{4}$/,
  usa: /^(\+?1)?[\s\-]?\(?\d{3}\)?[\s\-]?\d{3}[\s\-]?\d{4}$/,
};
/* eslint-enable */

const onNextClick = async () => {
  if (country.value) {
    if (
      !phoneRegexes[country.value.value].test(country.value.code + phone.value) // –≤–∞–ª–∏–¥–∏—Ä—É–µ–º –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞
    ) {
      phoneError.value = "–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –Ω–æ–º–µ—Ä–∞";
      return;
    }
    phoneError.value = "";
    try {
      isLoading.value = true;
      const res = await signInService.createCode(
        country.value.code + phone.value
      );
      if (res.success) {
        store.availableChannels = res.data.clientChannels;
        store.sessionId = res.data.sessionId;
        store.sentTo = res.data.clientChannels.find(
          (i) => i.type === res.data.sentTo
        );
        emit("onNext");
      }
    } catch (e) {
      console.log(e);
    } finally {
      isLoading.value = false;
    }
  }
};

watch(phone, () => {
  phoneError.value = ""; // —á—Ç–æ–±—ã —Å–±—Ä–∞—Å—ã–≤–∞—Ç—å –æ—à–∏–±–∫—É –ø—Ä–∏ –≤–≤–æ–¥–µ
});
</script>

<template>
  <div class="wrapper">
    <h1 class="title">{{ t("–í–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞") }}</h1>
    <p class="subtitle">{{ t("–ß—Ç–æ–±—ã –≤–æ–π—Ç–∏ –∏–ª–∏ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è") }}</p>
    <CountrySelect v-model="country" />
    <PhoneInput
      v-model="phone"
      :code="country ? country.code : ''"
      :error="phoneError"
    />
    <v-btn
      variant="flat"
      color="#007AFF"
      height="55"
      class="continue-btn"
      :disabled="!phone"
      @click="onNextClick"
      >{{ t("–ü—Ä–æ–¥–æ–ª–∂–∏—Ç—å") }}</v-btn
    >
  </div>
</template>

<style scoped>
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.title {
  text-align: center;
  margin-bottom: 14px;
}
.subtitle {
  color: #666666;
  text-align: center;
  margin-bottom: 30px;
}
.phone-input {
  margin-bottom: 50px;
}
