<script setup lang="ts">
import { onMounted, ref } from "vue";
import { useI18n } from "vue-i18n";

const currentLanguage = ref("–†—É—Å—Å–∫–∏–π");

const languages = ["–†—É—Å—Å–∫–∏–π", "English"];

const i18n = useI18n();

const onLanguageChange = (value: string) => {
  switch (value) {
    case "–†—É—Å—Å–∫–∏–π":
      i18n.locale.value = "ru";
      localStorage.setItem("locale", "ru");
      break;
    case "English":
      i18n.locale.value = "en";
      localStorage.setItem("locale", "en");
      break;
  }
};

onMounted(() => {
  const locale = localStorage.getItem("locale");
  if (locale) {
    currentLanguage.value = locale === "ru" ? "–†—É—Å—Å–∫–∏–π" : "English";
    i18n.locale.value = locale;
  }
});
</script>

<template>
  <div class="d-flex align-center justify-space-between w-100">
    <v-select
      v-model="currentLanguage"
      :items="languages"
      variant="plain"
      density="compact"
      hide-details
      max-width="80"
      @update:model-value="onLanguageChange"
    >
      <template #selection="{ item }">
        <p>{{ item.value }}</p>
      </template>
    </v-select>
    <div class="d-flex align-center ga-6">
      <router-link to="">{{ i18n.t("–£—Å–ª–æ–≤–∏—è") }}</router-link>
      <router-link to="">{{ i18n.t("–ö–æ–Ω—Ñ–∏–¥–µ–Ω—Ü–∏–∞–ª—å–Ω–æ—Å—Ç—å") }}</router-link>
    </div>
  </div>
</template>

